  font-family: "Young Style", sans-serif;
  font-family: "Young Style Underlines", sans-serif;
  src: url("https://fonts.cdnfonts.com/css/young-style") format("woff2");
}
#app {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* Responsive layout - when the screen is less than 800px wide, make the two columns stack on top of each other instead of next to each other (and change the direction - make the "cart" column go on top) */
}
#app #title {
  font-family: "Young Style", sans-serif;
  color: #4a3f35;
}
#app #final-cart {
  display: flex;
  flex-direction: column;
}
#app #final-cart div {
  display: grid;
  grid-template-columns: 60% 20% 20%;
  margin-bottom: 20px;
}
#app #final-cart div p {
  font-family: "Courier New", Courier, monospace;
  padding-right: 30px;
}
#app #totals {
  width: 40%;
  display: grid;
  grid-template-columns: 50% 25% 25%;
}
#app .row {
  display: -ms-flexbox; /* IE10 */
  display: flex;
  -ms-flex-wrap: wrap; /* IE10 */
  flex-wrap: wrap;
  margin: 0 -16px;
}
#app .col-25 {
  -ms-flex: 25%; /* IE10 */
  flex: 25%;
}
#app .col-50 {
  -ms-flex: 50%; /* IE10 */
  flex: 50%;
}
#app .col-75 {
  -ms-flex: 75%; /* IE10 */
  flex: 75%;
}
#app .col-25,
#app .col-50,
#app .col-75 {
  padding: 0 16px;
}
#app .container {
  background-color: #f2f2f2;
  padding: 5px 20px 15px 20px;
  border: 1px solid lightgrey;
  border-radius: 3px;
}
#app input[type=text] {
  width: 100%;
  margin-bottom: 20px;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 3px;
}
#app label {
  margin-bottom: 10px;
  display: block;
}
#app .icon-container {
  margin-bottom: 20px;
  padding: 7px 0;
  font-size: 24px;
}
#app .btn {
  background-color: #4a3f35;
  color: white;
  padding: 12px;
  margin: 10px 0;
  border: none;
  width: 100%;
  border-radius: 3px;
  cursor: pointer;
  font-size: 17px;
}
#app .btn:hover {
  background-color: darkred;
  color: #fff7e6;
}
#app span.price {
  float: right;
  color: grey;
}
@media (max-width: 800px) {
  #app .row {
    flex-direction: column-reverse;
  }
  #app .col-25 {
    margin-bottom: 20px;
  }
